export function getFlagType(type){
    var flag;
    switch (parseInt(type)) {
        case 3: case 8:
            flag=1;
            break;
        case 4:
            flag=2;
            break;
        case 9:
            flag=2;
            break;
        case 6:
            flag=3;
            break;
        case 11:
            flag=4;
            break;
        case 12:
            flag=5;
            break;
        case 13:
            flag=6;
            break;
        case 14:
            flag=8;
            break;
        case 15: case 16:
            flag=9;
            break;
        case 17:
            flag=10;
            break;
    }
    return flag;
}
