import alarmhdtable from './alarm_hdtable.js';
import {getAlarmInfo,filterData} from './alarm_info.js'
export const alarmMixin={
  data(){
    return{
        hot: {},//hottable
        data: [],//hottabledata
        changeList:{
          changeData:{},//
          sortMacList:[],//
          sortDgmList:[],//
        },
        timer: null,//
        scroll:0,//
    }
  },
  mounted(){
    this.getListData(this.type)
  },
  //
  beforeDestroy() {
    let HotTable = document.getElementById('HotTable');
    $(HotTable).empty();
    this.data = [];
    if(!$.isEmptyObject(this.hot)){
      this.hot.destroy();
    }
    if(this.timer){
      clearTimeout(this.timer);
    }
  },
  beforeRouteLeave (to, from, next) {
    let changeData = this.changeList.changeData;
    //
    if(!$.isEmptyObject(changeData)){
      swal({
        title: "",
        text: ``,
        buttons: {
          sure: "",
          cancel: "",
        },
        dangerMode: true,
      })
          .then((willDelete) => {
            if (willDelete) {
              next();
            } else {
              window.history.go(1);
            }
          });
    }
    else{
      next();
    }
  },
  methods:{
    getListData(type){
      let loadTime = false;
      let that = this;
      this.timer = setTimeout(function(){//
        if(!loadTime){
          that.$parent.loading = true;//
        }
      },500)
      this.$getApi.queryAllAlarmInfobyType(type,this.$store.state.tids)
      .then(res=>{
        loadTime = true;
        this.$parent.loading = false;//
        let result = getAlarmInfo(res,type);
        this.data = result;
        if(!result.length){
          this.$emit("child-load",false);
          this.$emit("child-msg",true)//
        }else{

          //
          this.$emit("child-msg",false);//
          this.renderTable()
        }
      })
          .catch(err=>{
            console.log('' ,err)
          })
    },
    /*  */
    renderTable(){
      if(this.$route.path.indexOf('alarm')<0){//
        return;
      }
      let HotTable = document.getElementById('HotTable');
      let property = {
        $this:this,
        type:this.type,
        getData:this.getData,
        getHot:this.getHot,
        changeList:this.changeList,
        reRenderSelectOption:this.reRenderSelectOption,
      }
      //table
      this.hot = new alarmhdtable(HotTable,property);
      this.reRenderSelectOption();
      /*  */
      let wtHolder = document.getElementsByClassName('wtHolder')[0]
      wtHolder.addEventListener('scroll',(e)=>{
        this.hot.destroyEditor()//
      });
      this.filterRows(this.hot,this.data)
    },
    /**/
    reRenderSelectOption(){ //selectOption,option
      let cellMeta  = this.hot.getCellMetaAtRow(0);
      let rows = this.data.length;
      
      cellMeta.forEach(item=>{
        if(item.prop == "alarmBaseInfo.alarmInfo.name"){ //
          for(let i=0;i<rows;i++){
            this.hot.setCellMeta(i,item.col,"source",this.data[this.hot.toPhysicalRow(i)].alarmBaseInfo.valueSources)
          }
        }
        if(this.type==2){//
          for(let i=0;i<rows;i++){
            let rowNum = this.hot.toPhysicalRow(i);
            if((this.data[rowNum].alarmBaseInfo.dgm_type==2 || this.data[rowNum].alarmBaseInfo.dgm_type==1) && this.data[rowNum].alarmBaseInfo.pos_type==5){
              this.hot.setCellMeta(i,item.col,"readOnly",true);
            }
            if(this.data[rowNum].alarmBaseInfo.pos_type==5 && (item.prop=='alarmBaseInfo.baseInfo.ll_limit' || item.prop=='alarmBaseInfo.baseInfo.l_limit')){
              this.hot.setCellMeta(i,item.col,"readOnly",true);
            }
          }
        }
        if(this.type==3){//MHD
          if(item.col==5){//
            for(let i=0;i<rows;i++){
              let rowNum = this.hot.toPhysicalRow(i);
              if(this.data[rowNum].alarmBaseInfo.code==2000){
                this.hot.setCellMeta(i,item.col,"prop","alarmBaseInfo.alarm_threshold_index_val");
                this.hot.setCellMeta(i,item.col,"editor","dropdown");
                this.hot.setCellMeta(i,item.col,"strict",true);
                this.hot.setCellMeta(i,item.col,"source",this.data[this.hot.toPhysicalRow(i)].alarmBaseInfo.alarmIndexSources)
              }
              else{
                this.hot.setCellMeta(i,item.col,"prop","alarmBaseInfo.vib_k_hh");
                this.hot.setCellMeta(i,item.col,"type","text");
              }
            }
          }
          if(item.prop == "alarmBaseInfo.code_val"){ //
            for(let i=0;i<rows;i++){
              this.hot.setCellMeta(i,item.col,"source",this.data[this.hot.toPhysicalRow(i)].alarmBaseInfo.codeSources)
            }
          }
        }
      })
    },
    //
    filterRows(hot,data){
      const filtersPlugin = hot.getPlugin('filters');
      if(this.type == 1 && sessionStorage.getItem('filter_alarm1')){
        let filterArr =  JSON.parse(sessionStorage.getItem('filter_alarm1'));
        filtersPlugin.addCondition(filterArr[0].column,filterArr[0].conditions[0].name,filterArr[0].conditions[0].args,filterArr[0].operations);
        filtersPlugin.filter();
      }
      else if(this.type == 2 && sessionStorage.getItem('filter_alarm2')){
        let filterArr =  JSON.parse(sessionStorage.getItem('filter_alarm2'));
        filtersPlugin.addCondition(filterArr[0].column,filterArr[0].conditions[0].name,filterArr[0].conditions[0].args,filterArr[0].operations);
        filtersPlugin.filter();
      }
      else if(this.type == 3 && sessionStorage.getItem('filter_alarm3')){
        let filterArr =  JSON.parse(sessionStorage.getItem('filter_alarm3'));
        filtersPlugin.addCondition(filterArr[0].column,filterArr[0].conditions[0].name,filterArr[0].conditions[0].args,filterArr[0].operations);
        filtersPlugin.filter();
      }
    },
    //data
    getData(){
      return this.data
    },
    /* hot */
    getHot(){
      return this.hot
    },
  }
}
